import React, { useEffect, useState } from 'react'
import './style.css'

import { useHistory, useLocation, useParams } from 'react-router-dom';

import { userActiveLevelDetailsStore } from '../../../../store/ActiveLevelDetailsStore';
import { userActiveSubLevelDetailsStore } from '../../../../store/ActiveSubLevelDetailsStore';

import { useActiveScreen } from '../../../../store/ActiveScreen';

import * as defaulttask from '../../../../assets/close_icon.svg';
import * as video from '../../../../assets/watch.svg';
import * as activity from '../../../../assets/activity.svg';
import * as quiz from '../../../../assets/quiz.svg';
import * as info from '../../../../assets/close_icon.svg';
import * as tick from '../../../../assets/tick.svg';

import { useLanguageStore } from '../../../../store/LanguageStore';
import { useColourObjectStore } from '../../../../store/ColourStore';


const Level = (props: any) => {

  const { aniout, setaniout } = props;

  const { colourObj } = useColourObjectStore();

  const { ActiveLevelDetails, loadActiveleveldetails } = userActiveLevelDetailsStore();
  const { loadActiveSubleveldetails } = userActiveSubLevelDetailsStore();
  const { Language, setLanguage } = useLanguageStore();

  const history = useHistory();

  const { setActiveScreen } = useActiveScreen();

  const search = useLocation().search;

  let params = useParams();


  useEffect(() => {

    setaniout(false)

    if (!ActiveLevelDetails) {
      loadActiveleveldetails(localStorage.getItem("ActiveLevel"))
    }

    if (params.instanceId) {
      const id = new URLSearchParams(search).get('id');
      if (id) {
        console.log("====== the id is", id);
        history.replace(`/${params.instanceId}/`)
      }
    }

    // if (params.instanceId) {
    //   loadlevelwithid(params.instanceId)
    // }
    // else {
    //   if (props.id) {
    //     loadlevelwithid(props.id)
    //   }
    // }

    // const id = new URLSearchParams(search).get('id');
    // if (id) {
    //   console.log("======>>>", id)
    //   loadActiveleveldetailsbyqr(id)
    //   setActiveScreen("Level")
    // }

  }, [])

  const openSubLevel = (obj: any) => {
    console.log(obj);
    setActiveScreen("SubLevel")
    loadActiveSubleveldetails(obj.id)
    localStorage.setItem("ActiveSubLevel", obj.id)
  }

  const taskicon = (tasktype: any) => {
    if (tasktype === "VIDEO") {
      return video
    } else if (tasktype === "QUIZ") {
      return quiz
    } else if (tasktype === "ACTIVITIES") {
      return activity
    } else if (tasktype === "DID YOU KNOW") {
      return info
    } else {
      return defaulttask
    }
  }

  // animations 
  const fade = (props) => {
    let result = (aniout ? (props + " " + "fade_out") : (props))
    return result
  }

  const slideup = (props) => {
    let result = (aniout ? (props + " " + "slide_up") : (props))
    return result
  }

  const calcdelay = (props) => {
    let result = ((props * 0.10)) + "s"
    return result
  }


  return (
    <>
      <div className="tasklist_bottommaincontainer">

        <div className="tasklist_about">
          <div className={slideup('bg_circle3 shadow')} style={{ backgroundColor: colourObj.color_m2 }}></div>
          <p className={fade("taskscreen_description")}>
            {(Language?.value === "KANNADA") ? (ActiveLevelDetails) && ActiveLevelDetails.TextData[0]?.body : (ActiveLevelDetails) && ActiveLevelDetails.l_body}
          </p>
        </div>

        <div className={fade("tasklist_listitem_container")} >

          {(ActiveLevelDetails) && ActiveLevelDetails.SubLevelData.map((object: any, index: any) => {
            console.log((ActiveLevelDetails) && ActiveLevelDetails.SubLevelData[index - 1]?.GameData[0] === undefined);

            return (
              <div className={(object?.GameData[0] !== undefined && index !== 0) ? "tasklist_listitem"
                : ((ActiveLevelDetails) && ActiveLevelDetails.SubLevelData[index - 1] && ActiveLevelDetails.SubLevelData[index - 1]?.GameData[0] === undefined) ? "tasklist_listitem disabled" : "tasklist_listitem"}
                onClick={() => {
                  setaniout(true);
                  setTimeout(() => {
                    openSubLevel(object)
                  }, 290);
                }} style={aniout ? {} : { animationDelay: calcdelay(index) }}>
                <div className="tasklist_sub_level_container">
                  <div className="tasklist_listnumber_wrapper" style={{ backgroundColor: colourObj.color_m1 }}>
                    <div className="tasklist_listnumber">{index + 1}</div>
                  </div>
                  <div className="tasklist_listtext_wrapper" style={{ backgroundColor: colourObj.color_s2 }}>
                    <div className="tasklist_listtext">{(Language?.value === "KANNADA") ? object.TextData[0]?.title : object.sl_title}</div>
                  </div>
                  <div className={'tasklist_button_wrapper' + ' ' + object.sl_types}
                    style={(object.sl_types === "VIDEO") ? { backgroundColor: colourObj.color_11 }
                      :
                      ((object.sl_types === "QUIZ") ? { backgroundColor: colourObj.color_12 }
                        :
                        ((object.sl_types === "ACTIVITIES") ? { backgroundColor: colourObj.color_13 }
                          :
                          ((object.sl_types === "DID YOU KNOW") ? { backgroundColor: colourObj.color_14 }
                            :
                            {})
                        )
                      )
                    }
                  >
                    <img className='tasklist_button' src={taskicon(object.sl_types)} />
                    {(object?.GameData[0]?.status === "DONE") && <img className='done_icon' src={tick} />}
                  </div>
                </div>
              </div>
            )
          })}
        </div>
      </div>
    </>
  )
}


export default Level
